import {
  me,
  myGroups,
  readTeam,
  getMyExtention,
  saveMyExtention,
  getOneDriveFolder,
  createOneDriveFolder,
  createOneDriveFolderChild,
  getOneDriveFile,
  saveOneDriveFile,
} from "./OfficeGraph";

import projectInfo from "../../package.json";

export const extentionName = "com.ztickybar.tools";
export const OURPATH = "Apps/jumpto365.com";
export const OURCONFIGFILENAME = "launchpad.json";

export function saveCache(memberships) {
  return new Promise((resolve, reject) => {
    var membershipIndex = [];
    var queue = 0;
    memberships.forEach((membership) => {
      membershipIndex.push(membership.id);
      queue++;

      saveOneDriveFile(
        OURPATH,
        cachedFileName(membership.id),
        membership
      ).then(() => {
        queue--;
        if (queue < 1) {
          saveOneDriveFile(OURPATH, "memberships.json", membershipIndex)
            .then(() => {
              resolve(membershipIndex);
            })
            .catch((e) => {
              reject(e);
            });
        }
      });
    });
  });
}

function cachedFileName(id) {
  return "membership." +id + ".json";
}

export function readCache() {
return new Promise(async (resolve, reject) => {
  var membershipIndex = await getOneDriveFile(OURPATH, "memberships.json").catch(e=>{reject(e)})
  if (!membershipIndex) return
  var queue = 0;
  var memberships = []
  membershipIndex.forEach(id=>{
    queue++
    getOneDriveFile(OURPATH, cachedFileName(id)).then(data=>{
      
      memberships.push(data)
      queue--;
      if (queue < 1) {
        resolve(memberships)
      }
    }).catch(e=>{reject(e)})
  })

  
})


  
}

/**
 * Verifies access to all endpoints used for the LaunchPad app
 *
 * @param {*} setCheckMe Callback accepting a string - "OK" or an error
 * @param {*} setCheckMyExtention  Callback accepting a string - "OK" or an error
 * @param {*} setCheckOneDrive  Callback accepting a string - "OK" or an error
 * @param {*} setCheckOneDriveFiles  Callback accepting a string - "OK" or an error
 * @param {*} setCheckMemberships  Callback accepting a string - "OK" or an error
 * @param {*} setCheckTeam  Callback accepting a string - "OK" or an error
 */
export async function verifyAccessForLanuchPad(
  setCheckMe,
  setCheckMyExtention,
  setCheckOneDrive,
  setCheckOneDriveFiles,
  setCheckMemberships,
  setCheckTeam
) {
  me()
    .then((m) => {
      setCheckMe("OK");
    })
    .catch((e) => {
      setCheckMe(e.message);
    });

  var extention = await saveMyExtention(extentionName, {
    hello: "world",
  }).catch((e) => {
    setCheckMyExtention(e.message);
  });
  if (extention || extention === "") {
    setCheckMyExtention("OK");
  }

  getOneDriveFolder(OURPATH)
    .then((r) => {
      setCheckOneDrive("OK");
      checkAccessToConfig();
    })
    .catch(async (e) => {
      var f = await getOneDriveFolder("Apps").catch((e) => {});
      if (f) {
        var child = await createOneDriveFolderChild(
          "Apps",
          "jumpto365.com"
        ).catch((e) => {
          setCheckOneDrive(e.message);
        });
        if (child) {
          setCheckOneDrive("OK");
          checkAccessToConfig();
        }
      } else {
        var child = await createOneDriveFolder("Apps").catch((e) => {
          setCheckOneDrive(e.message);
        });
        child = await createOneDriveFolderChild("Apps", "jumpto365.com").catch(
          (e) => {
            setCheckOneDrive(e.message);
          }
        );
        if (child) {
          setCheckOneDrive("OK");
          checkAccessToConfig();
        }
      }
    });

  myGroups()
    .then((memberships) => {
      setCheckMemberships("OK");
      var teamId;
      memberships.forEach((member) => {
        if (member.displayName) {
          if (!teamId) {
            teamId = member.id;
          }
        }
      });
      if (teamId) {
        readTeam(teamId)
          .then((m) => {
            setCheckTeam("OK");
          })
          .catch((e) => {
            setCheckTeam(e.message);
          });
      } else {
        setCheckTeam("Didn't find any Microsoft Teams memberships");
      }
    })
    .catch((e) => {
      setCheckMemberships(e.message);
    });

  async function checkAccessToConfig() {
    var settings = await getOneDriveFile(OURPATH, OURCONFIGFILENAME).catch(
      async (e) => {
        var file = await saveOneDriveFile(OURPATH, OURCONFIGFILENAME, {
          version: projectInfo.version,
        }).catch((e) => {
          setCheckOneDriveFiles(e.message);
        });
        if (file) {
          setCheckOneDriveFiles("OK");
        }
      }
    );
    if (settings) {
      setCheckOneDriveFiles("OK");
    }
  }
}
